<template>
<div v-if="canShow()" class="panel">
    <div class="page__bd">
        <div v-for="item in meetingList" class="weui-panel">
            <div class="weui-panel__hd">No.{{item.meetno}} {{getType(item.meetingType)}}</div>
            <div class="weui-panel__bd">
                <router-link :to="getUrl(item)" class="weui-media-box weui-media-box_appmsg">
                    <div class="weui-media-box weui-media-box_text">
                        <h4 class="weui-media-box__title">{{item.bu}} - {{item.meetingTitle}}</h4>
                        <br>
                        <ul class="weui-media-box__info">
                            <li class="weui-media-box__info__meta">发起人:</li>
                            <li class="weui-media-box__info__meta">{{item.author}}</li>
                        </ul>
                    </div>
                </router-link>
            </div>
        </div>

    </div>
</div>

<div v-else class="weui-msg">
    <div class="weui-msg__icon-area"><i class="weui-icon-warn weui-icon_msg"></i></div>
    <div class="weui-msg__text-area">
        <h2 class="weui-msg__title">权限认证失败</h2>
        <p class="weui-msg__desc">您没有该模块的权限</p>
    </div>
    <div class="weui-msg__extra-area">
        <p class="weui-footer__links">
            <a href="#" class="weui-footer__link">奥克斯微信企业号</a>
        </p>
        <p class="weui-footer__text">Copyright © 2015-2017 open.auxgroup.com</p>
    </div>
</div>

</template>

<script>
import server from '../../scripts/display.js'


export default {

    data() {
        return {
            badge: this.$store.state.user.badge,
            meetingList: [],
            value: '',
            search: 'false'
        };
    },

    mounted() {

        var this2 = this;

        server.meetingList().then(function(response) {

            this2.meetingList = response.data;

        }).catch(function(error) {

            console.log(error);
        
        });
    },

    methods: {

        canShow : function(){

            if(this.$store.state.user.badge == '0'
                ||this.$store.state.user.badge == '150209001'
                ||this.$store.state.user.badge == '030200478'
                ||this.$store.state.user.badge == '140408002') 

            return true;

            return false;
        },

        getType: function(tag) {
            var s = '';
            if (tag == '1')
                s = '两大课件';
            else
                s = '会议心得';
            return s;
        },

        getUrl: function(item) {
            //return '/ratermain?meetingId=' + item.meetingId +'&ratelevel='+item.ratelevel;
            return '/display/participants?meetingId='+item.meetingId;
        }
    },

    computed: {

    }
};
</script>

<style scoped>

.weui-media-box__title{
  white-space: normal;
}

.weui-media-box {
    padding: 10px 10px 5px 10px;
}

.page-tabbar {
    overflow: hidden;
    height: 100vh;
}

.page-wrap {
    overflow: auto;
    height: 100%;
    padding-bottom: 100px;
}

.weui-tabbar__icon {
    display: inline-block;
    width: 27px;
    height: 27px;
}

.expireDay1 {
    display: inline-block;
    padding: .25em .4em;
    min-width: 8px;
    border-radius: 18px;
    color: black;
    line-height: 1.2;
    text-align: center;
    font-size: 15px;
    vertical-align: middle;
    margin-left: 30px;
}

.weui-tabbar__label {
    text-align: center;
    color: green;
    font-size: 12px;
    line-height: 1.8;
}

.weui-tabbar__item {
    background-color: #F2F4F3;
}
</style>



